import firebase from "firebase/compat/app";
import "firebase/compat/database";
import firebaseConfig from "./firebaseConfig";

import React, { useState, useEffect } from "react";
import ScrollContainer from "react-indiana-drag-scroll";
import { Chip, Typography } from "@mui/joy";
import "./Category.css";

function Category({ handleSelectedQuestions }) {
  const items = [
    " Today's",
    " Maker's Pick",
    "  ",
    " ",
    " ",
    " ",
    " ",
    " ",
    " ",
    " ",
  ];

  const [selectedItem, setSelectedItem] = useState(" Today's");
  const [questions, setQuestions] = useState([]);

  useEffect(() => {
    if (selectedItem === " Today's") {
      const QuestionsRef = firebase.database().ref("QA");
      QuestionsRef.once("value", (snapshot) => {
        const questions = [];
        snapshot.forEach((childSnapshot) => {
          const question = childSnapshot.val();
          questions.push(question);
        });

        const len = questions.length;
        const now = new Date();

        const index1 = Math.floor(len / now.getDate());
        const index2 = Math.floor(len / 3 + len / now.getDate());
        const index3 = Math.floor((2 * len) / 3 + len / now.getDate());

        const selectedQuestions = [
          questions[index1],
          questions[index2],
          questions[index3],
        ];
        handleSelectedQuestions(selectedQuestions);
      });
    } else {
      setQuestions([]);
    }
  }, [selectedItem]);

  const handleItemClick = (item) => {
    setSelectedItem(item);
    if (item === " Today's") {
      const QuestionsRef = firebase.database().ref("QA");
      QuestionsRef.once("value", (snapshot) => {
        const questions = [];
        snapshot.forEach((childSnapshot) => {
          const question = childSnapshot.val();
          questions.push(question);
        });

        const len = questions.length;
        const now = new Date();

        const index1 = Math.floor(len / now.getDate());
        const index2 = Math.floor(len / 3 + len / now.getDate());
        const index3 = Math.floor((2 * len) / 3 + len / now.getDate());

        const selectedQuestions = [
          questions[index1],
          questions[index2],
          questions[index3],
        ];
        handleSelectedQuestions(selectedQuestions);
      });
    }
    if (item === " Maker's Pick") {
      const QuestionsRef = firebase
        .database()
        .ref("QA")
        .orderByChild("my_pick")
        .equalTo(1);
      QuestionsRef.once("value", (snapshot) => {
        let questions = [];
        snapshot.forEach((childSnapshot) => {
          let question = childSnapshot.val();
          questions.push(question);
        });

        if (questions.length >= 3) {
          for (let i = questions.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [questions[i], questions[j]] = [questions[j], questions[i]];
          }
          questions = questions.slice(0, 3);
        }

        handleSelectedQuestions(questions);
      });
    }

    if (item === " ") {
      const QuestionsRef = firebase
        .database()
        .ref("QA")
        .orderByChild("eng")
        .equalTo(1);
      QuestionsRef.once("value", (snapshot) => {
        let questions = [];
        snapshot.forEach((childSnapshot) => {
          let question = childSnapshot.val();
          questions.push(question);
        });

        if (questions.length >= 3) {
          for (let i = questions.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [questions[i], questions[j]] = [questions[j], questions[i]];
          }
          questions = questions.slice(0, 3);
        }

        handleSelectedQuestions(questions);
      });
    }
    if (item === " ") {
      const QuestionsRef = firebase
        .database()
        .ref("QA")
        .orderByChild("animal")
        .equalTo(1);
      QuestionsRef.once("value", (snapshot) => {
        let questions = [];
        snapshot.forEach((childSnapshot) => {
          let question = childSnapshot.val();
          questions.push(question);
        });

        if (questions.length >= 3) {
          for (let i = questions.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [questions[i], questions[j]] = [questions[j], questions[i]];
          }
          questions = questions.slice(0, 3);
        }

        handleSelectedQuestions(questions);
      });
    }
    if (item === " ") {
      const QuestionsRef = firebase
        .database()
        .ref("QA")
        .orderByChild("king")
        .equalTo(1);
      QuestionsRef.once("value", (snapshot) => {
        let questions = [];
        snapshot.forEach((childSnapshot) => {
          let question = childSnapshot.val();
          questions.push(question);
        });

        if (questions.length >= 3) {
          for (let i = questions.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [questions[i], questions[j]] = [questions[j], questions[i]];
          }
          questions = questions.slice(0, 3);
        }

        handleSelectedQuestions(questions);
      });
    }
    if (item === " ") {
      const QuestionsRef = firebase
        .database()
        .ref("QA")
        .orderByChild("plant")
        .equalTo(1);
      QuestionsRef.once("value", (snapshot) => {
        let questions = [];
        snapshot.forEach((childSnapshot) => {
          let question = childSnapshot.val();
          questions.push(question);
        });

        if (questions.length >= 3) {
          for (let i = questions.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [questions[i], questions[j]] = [questions[j], questions[i]];
          }
          questions = questions.slice(0, 3);
        }

        handleSelectedQuestions(questions);
      });
    }
    if (item === " ") {
      const QuestionsRef = firebase
        .database()
        .ref("QA")
        .orderByChild("food")
        .equalTo(1);
      QuestionsRef.once("value", (snapshot) => {
        let questions = [];
        snapshot.forEach((childSnapshot) => {
          let question = childSnapshot.val();
          questions.push(question);
        });

        if (questions.length >= 3) {
          for (let i = questions.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [questions[i], questions[j]] = [questions[j], questions[i]];
          }
          questions = questions.slice(0, 3);
        }

        handleSelectedQuestions(questions);
      });
    }
    if (item === " ") {
      const QuestionsRef = firebase
        .database()
        .ref("QA")
        .orderByChild("english")
        .equalTo(1);
      QuestionsRef.once("value", (snapshot) => {
        let questions = [];
        snapshot.forEach((childSnapshot) => {
          let question = childSnapshot.val();
          questions.push(question);
        });

        if (questions.length >= 3) {
          for (let i = questions.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [questions[i], questions[j]] = [questions[j], questions[i]];
          }
          questions = questions.slice(0, 3);
        }

        handleSelectedQuestions(questions);
      });
    }
    if (item === " ") {
      const QuestionsRef = firebase
        .database()
        .ref("QA")
        .orderByChild("religion")
        .equalTo(1);
      QuestionsRef.once("value", (snapshot) => {
        let questions = [];
        snapshot.forEach((childSnapshot) => {
          let question = childSnapshot.val();
          questions.push(question);
        });

        if (questions.length >= 3) {
          for (let i = questions.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [questions[i], questions[j]] = [questions[j], questions[i]];
          }
          questions = questions.slice(0, 3);
        }
        handleSelectedQuestions(questions);
      });
    } else {
      setQuestions([]);
    }
  };

  return (
    <ScrollContainer className="Category_list">
      {items.map((item, index) => (
        <div className="item" key={index}>
          <Chip
            className="chip"
            color="info"
            onClick={() => handleItemClick(item)}
            variant={selectedItem === item ? "solid" : "outlined"}
          >
            <div className="chip-label">{item}</div>
          </Chip>
        </div>
      ))}
    </ScrollContainer>
  );
}

export default Category;
