import logo from "./logo.svg";
import "./App.css";
import { React, useState, useEffect, useRef } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";

import Header from "./Header";
import Category from "./Category";
import Quiz from "./Quiz";
import SharedQuiz from "./SharedQuiz";

import Footer from "./Footer";

function App() {
  const [selectedQuestions, setSelectedQuestions] = useState([]);

  const handleSelectedQuestions = (questions) => {
    setSelectedQuestions(questions);
  };

  return (
    <BrowserRouter>
      <div className="Main">
        <Header />
        <Routes>
          <Route
            path="/"
            element={
              <div>
                <Category handleSelectedQuestions={handleSelectedQuestions} />
                <Quiz selectedQuestions={selectedQuestions} />
              </div>
            }
          />
          <Route path="/shared-quiz" element={<SharedQuiz />} />
        </Routes>
        <Footer id="footer" />
      </div>
    </BrowserRouter>
  );
}

export default App;
