import React, { useEffect, useState } from "react";
import "./SharedQuiz.css";
import Button from "@mui/joy/Button";
import { useNavigate, useLocation } from "react-router-dom";
import ReactCardFlip from "react-card-flip";
import { Card } from "@mui/joy";
import firebase from "firebase/compat/app";
import "firebase/compat/database";
import firebaseConfig from "./firebaseConfig";

function SharedQuiz() {
  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  const num = searchParams.get("num");

  const navigate = useNavigate();
  const [question, setQuestion] = useState("");
  const [answer, setAnswer] = useState("");
  const [isFlipped, setIsFlipped] = useState(false);

  const handleFlipCard = () => {
    setIsFlipped(!isFlipped);
  };

  useEffect(() => {
    // Firebase 
    firebase.initializeApp(firebaseConfig);

    // Firebase Realtime Database num  
    const database = firebase.database();
    const qaRef = database.ref("QA");

    qaRef.once("value").then((snapshot) => {
      const questions = snapshot.val();
      const questionData = questions[num];
      setQuestion(questionData.que);
      setAnswer(questionData.ans);
    });
  }, [num]);

  const handleShare = () => {
    if (navigator.share) {
      navigator
        .share({
          title: ",  ",
          url: window.location.href,
        })
        .then(() => {
          console.log("URL  ");
        })
        .catch((error) => {
          console.error("URL  ", error);
        });
    } else {
      console.log("URL     .");
    }
  };

  const handle_go_main = () => {
    navigate("/hiyoumore");
  };

  return (
    <div className="SharedQuiz_Frame">
      <p className="text">
          
        <br />  ?
      </p>

      <ReactCardFlip isFlipped={isFlipped} flipDirection="vertical">
        <Card className="Card_Front" onClick={handleFlipCard}>
          <div className="Card_Front_Que">{question}</div>
        </Card>
        <Card className="Card_Back" onClick={handleFlipCard}>
          <div className="Card_Back_Ans">{answer}</div>
        </Card>
      </ReactCardFlip>
      <Button
        className="btn"
        color="info"
        onClick={handleShare}
        variant="solid"
      >
          
      </Button>
      <Button
        className="btn"
        color="info"
        onClick={handle_go_main}
        variant="solid"
      >
           
      </Button>
    </div>
  );
}

export default SharedQuiz;
